.App {
  /* text-align: center; */
}

h1, h2, h3, h4, h5, h6, p, span, input, li, th, td, button, div, label, title {
  font-family: khmerOsSiemReap!important;
  
}

.HeaderFont{
  font-family: khmerOsMuolLight!important;
  /* font-size: 9pt; */
  color: #014283;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo{
  width: 10px;
  padding: 10px 20%;
  cursor: pointer;
}

.img-logo{
  transition: transform 1s;
  /* box-shadow: 0px 0px 20px #000; */
  border-radius: 150px;
  width: 100px;
}

.ant-btn-primary{
  background: #005DAE!important;
  border-color: #005DAE!important;
  
}

.ant-btn-primary:hover{
  background: #4a645c!important;
  border-color: #4a645c!important;
  /* text-shadow: 0 -1px 0 rgb(0 0 0 / 12%);
    box-shadow: 0 2px 0 rgb(0 0 0 / 5%); */
}

.scale-animate{
  /* animation: rotate 0.5s 1 normal forwards; */
  animation: rotate 0.6s 2 normal forwards ease-in-out;
}
@keyframes rotate {
  0% {transform: scale(1,1);}
  50% {transform: scale(2,1.5);}
  /* 100% {transform: scale(1,1);} */
}